.heroSection {
    display: flex;
    width: 100%;
    height: 100vh;
    align-items: flex-end;
    padding: 5%;
    color: white;
    font-size: 4rem;
    font-family: 'Manrope';
}

h1 {
    font-weight:400;
}

.textLoop {
    color: aqua;
    font-weight:400;
    margin-bottom: 10%;
}

.frontendtext{
    display: flex;
    flex-direction: row;
    gap: 1rem;
}

@media (max-width: 900px) {
    .heroSection{
        font-size: x-large;
        padding: 30% 5%;
    }

    .frontendtext{
        display: flex;
        flex-direction: column;
        gap: 0;
    }

    h1 {
        font-weight:400;
        height: 250px;
    }
}